// @flow
import * as React from 'react';
import { fetchSellerPropertyForms } from './redux/Actions';
import { toastr } from 'react-redux-toastr';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { useEffect } from 'react';
import PropertyComponent from 'components/SellerProperty/Property/Loadable';
import Loader from 'components/Loader';
import _get from 'lodash/get';

type Props = {
  properties: Array<Object>,
  fetchSellerPropertyForms: Function,
  error: string,
  isLoading: boolean,
};

export const ListPropertyForm = (
  props: Props,
): React.Element<typeof React.Fragment> => {
  const { properties, isLoading, fetchSellerPropertyForms, error } = props;

  useEffect(() => {
    fetchSellerPropertyForms();
    // eslint-disable-next-line react-hooks/exhaustive-deps,
  }, []);

  useEffect(() => {
    if (error) {
      toastr.error('Something went wrong');
    }
  }, [error]);

  return (
    <React.Fragment>
      {isLoading && <Loader />}
      {properties.map((property, index) => {
        const { Forms: forms } = property;

        return (
          <PropertyComponent
            propertyIndex={index}
            property={property}
            forms={forms}
            key={index}
          />
        );
      })}
    </React.Fragment>
  );
};

const structuredSelector = createStructuredSelector({
  properties: state =>
    _get(state, ['sellerPropertyForms', 'propertyForms', 'Properties'], []),
  isLoading: state => state.sellerPropertyForms.isLoading,
  error: state => state.sellerPropertyForms.error,
});

const mapDispatchToProps = { fetchSellerPropertyForms };

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(ListPropertyForm): React.AbstractComponent<Props>);
